h1,h2,p {
  margin: 0; }

.title-content {
  font-size: 24px;
  line-height: 1.8em;
  @include phone {
    font-size: 4.5vw;
    line-height: 1.5em; } }

.mt-1 {
  margin-top: 10px; }

.mt-2 {
  margin-top: 18px; }

.mt-3 {
  margin-top: 25px; }

.logo-inline {
  @include bg('logo-inline.png');
  margin-top: -4px;
  margin-right: 3px; }

.df {
  display: flex;
  &.ai-c {
    align-items: center; }
  &.jc-c {
    justify-content: center; } }

.logo-inline2 {
  @include bg('logo-white-inline.png');
  margin-top: -4px;
  margin-right: 3px; }

.icon-fb {
  @include bg('icon-fb.png');
  margin: 0 10px; }

.icon-line {
  @include bg('icon-line.png');
  margin: 0 10px; }

.icon-phone2 {
  @include bg('icon-phone2.png');
  margin: 0 10px; }

.tempHide {
  transform: translate(0, 50px);
  opacity: 0;
  @include dTime(.8s);

  &.left {
    transform: translate(-50px, 0px); }
  &.right {
    transform: translate(50px, 0px); }
  &.top {
    transform: translate(0px, -50px); }
  &.show {
    opacity: 1;
    transform: translate(0, 0); } }
